const wordListUpper = wordList.map((x) => { return x.toUpperCase() }) //using the map method returns an array with uppercase words

function randomWord() {           //the following function returns a random world 
    return wordListUpper[Math.floor(Math.random() * wordListUpper.length)]
}


export { randomWord }; //exports random world callback 

